* {
    margin: 0;
}

.sidebar {
    width: 20%;
    background-color: #000;
    height: 697px;
    padding-top: 60px;
    color: white;
    text-align: center;
    font-family: poppins;
    float: left;
}

.brand-name {
    display: flex;
    padding: 10px;
    font-weight: 800;
    letter-spacing: 3px;
    justify-content: center;
}

.brand-name p {
    font-size: 25px;
    padding-left: 10px;
}

.sidebar-headings {
    padding: 0 10% 0 20%;
}

.sidebar-headings p {
    text-align: start;
    padding-left: 10px;
}

.flex-box {
    width: fit-content;
    display: flex;
    margin-top: 60px;
    align-items: center;
}

.link {
    transition: font-size 0.4s;
}

.link:hover {
    font-size: 20px;
}

.link:active {
    background-color: white;
}

.flex-box img {
    height: 35px;
    width: 35px;
}